import React, { createContext, FunctionComponent, useState, useCallback } from 'react';
import axios from 'axios';

import { IFiltersContext, IFiltersProvider } from './interfaces';

// filters context
const FiltersContext = createContext({} as IFiltersContext);

// filters provider
const FiltersProvider: FunctionComponent<IFiltersProvider> = ({ children }) => {
  // state
  const [ filters, setFilters ]: any = useState({});
  const [ inputs, setInputs ]: any = useState([]);
  const [ result, setResult ]: any = useState([]);

  // check item array
  const checkItemArray = useCallback((items: any [], item: any) => 
    (item !== '' && items.indexOf(item) === -1), []);

  // get filters posts
  const getFilters = useCallback((posts: any[]) => {
    if (posts instanceof Object === false) return false;

    const areas: any[] = [];
    const cities: any[] = [];
    const districts: any[] = [];
    const council: any[] = [];
    const neigh: any[] = [];
    const types: any[] = [];
    const zone: any[] = [];
    const values: any[] = [];

    for (let i = 0; i < posts.length; i++) {
      const item = posts[i];

      if (item instanceof Object) {
        const { acf: { area, cidade, conselho, distrito, freguesia, tipo, zona, valor } } = item;

        if (checkItemArray(areas, area))
          areas.push(area);

        if (checkItemArray(cities, cidade))
          cities.push(cidade);
          
        if (checkItemArray(districts, distrito))
          districts.push(distrito);
          
        if (checkItemArray(council, conselho))
          council.push(conselho);
          
        if (checkItemArray(neigh, freguesia))
          neigh.push(freguesia);
          
        if (checkItemArray(types, tipo))
          types.push(tipo);
          
        if (checkItemArray(zone, zona))
          zone.push(zona);

        if (checkItemArray(values, valor))
          values.push(valor);
      }
    }

    setInputs({ cities, council, districts, neigh, types, zone });
  }, [ checkItemArray, setInputs ]);

  // get filters query
  const getFiltersQuery = useCallback((values: any []) => {
    if (!values.length) {
      return setResult([]);
    }

    let queries: string = '';

    for (let value of values) {
      queries += `${value}&`;
    }

    axios.get(`houses?${queries}`).then(({ data }: any) => setResult(data));
  }, [ setResult ]);

  // create query
  const createQuery = useCallback((values: any []) => {
    const metas: any [] = [];

    for (let value of values) {
      if (value instanceof Object)
        metas.push(`filter[meta_key]=${value.id}&filter[meta_value]=${value.value}`);
    }
    
    getFiltersQuery(metas);
  }, [ getFiltersQuery ]);
  
  // get items
  const requestItems = useCallback((items: any []) => {
    const inputs: any [] = items.filter((item :any) => item.active === true);
    let values: any [] = [];

    for (let input of inputs) {
      if (input instanceof Object) {
        for (let value of input.items) {
          if (value instanceof Object && value.value !== '') {
            values.push(value);
          }
        }
      }
    }

    createQuery(values);
  }, [ createQuery ]);
  
  // clear
  const onClearInputs = useCallback((inputs: any) => {
    if (!Array.isArray(inputs) || !inputs.length) return false;
    
    const values: any = inputs;
    
    for (let i = 0; i < inputs.length; i++) {
      const { items } = inputs[i];
      
      for (let k = 0; k < items.length; k++) {
        values[i].items[k].value = '';
      }

      inputs[i].active = false;
    }
    
    console.log(inputs);
    
    setFilters(values);
    requestItems(values);
  }, [ requestItems ]);

  // on change
  const onChange = useCallback((value: any, id: number, inputs: any) => {
    if (!Array.isArray(inputs) || !inputs.length) return false;

    const values: any = inputs;

    for (let i = 0; i < inputs.length; i++) {
      let count = 0;
      const { items } = inputs[i];

      for (let k = 0; k < items.length; k++) {
        const input = items[k];

        if (id === input.id) {
          values[i].items[k].value = value;
        }

        if (values[i].items[k].value !== '') {
          count++;
        }
      }

      values[i].active = count > 0;
    }

    setInputs(values);
    requestItems(values);
  }, [ requestItems ]);

  // render
  return (
    <FiltersContext.Provider value={{
      result,
      filters,
      inputs,
      getFilters,
      getFiltersQuery,
      requestItems,
      onClearInputs,
      onChange
    }}>
      {children}
    </FiltersContext.Provider>
  );
};

export { FiltersContext, FiltersProvider };
export default FiltersProvider;